var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'3. Decisiones judiciales y recursos procesales'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 overflow-hidden"},[_vm._m(0),_vm._m(1),_vm._m(2),_vm._m(3),_vm._m(4),_vm._m(5),_vm._m(6),_vm._m(7),_vm._m(8),_vm._m(9),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Comprender esta tipología resulta esencial, pues cada actuación judicial debe entenderse en su contexto para identificar su finalidad, sus efectos jurídicos y las opciones que tienen las partes para ejercer su derecho de defensa y contradicción.")]),_c('div',{staticClass:"row align-items-start mb-5",attrs:{"data-aos":"fade-left"}},[_vm._m(10),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"La sentencia"}},[_c('p',[_vm._v("Es la decisión judicial más importante, dado que resuelve el conflicto jurídico planteado por las partes y pone fin a la instancia. Su objeto principal es decidir de manera definitiva, sobre las pretensiones de la demanda y las excepciones del demandado. Las sentencias pueden ser de fondo, cuando se resuelve sobre la materia litigiosa. Toda sentencia debe contener una estructura lógica. Una parte motiva, donde se exponen los hechos, los fundamentos jurídicos y la valoración de las pruebas, y una parte resolutiva, que contiene el fallo propiamente dicho. Además, debe ser congruente con lo pedido, motivada y dictada dentro de los plazos procesales. En la práctica, por ejemplo, si se adelanta un proceso de responsabilidad civil por daños y perjuicios, la sentencia debe analizar si hubo un hecho dañoso, si existe un nexo causal con la conducta del demandado y si procede indemnización. Una vez proferida la sentencia y si no es apelada, adquiere firmeza, hace tránsito a cosa juzgada y puede ser objeto de ejecución forzada (Cifuentes Muñoz, 2005).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Los autos interlocutorios"}},[_c('p',[_vm._v("Son providencias que, sin resolver el fondo del litigio, deciden sobre aspectos relevantes que pueden afectar derechos sustanciales o procesales de las partes. Estos autos suelen ser dictados en el curso del proceso para resolver cuestiones incidentales, tales como excepciones previas, solicitudes de pruebas, nulidades procesales, acumulación de procesos, medidas cautelares, desistimiento tácito o decisiones sobre pruebas anticipadas. Por ejemplo, si el demandado presenta una excepción de prescripción extintiva, el juez debe resolverla mediante un auto interlocutorio que, dependiendo de su contenido, podría terminar el proceso anticipadamente o permitir su continuación. Los autos interlocutorios, debido a su importancia, son generalmente susceptibles de recurso de apelación, lo que permite al superior jerárquico, revisar su legalidad y corregir eventuales errores (Cifuentes Muñoz, 2005).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Los autos de sustanciación"}},[_c('p',[_vm._v("Son providencias de trámite que tienen como finalidad permitir el desarrollo ordenado del proceso. Son decisiones que no resuelven aspectos de fondo ni controvertidos, y por ello su motivación suele ser breve o incluso innecesaria. Son ejemplos de autos de sustanciación: el que admite la demanda, el que ordena notificar una providencia, el que corre traslado de una solicitud o el que decreta un plazo para cumplir un requerimiento. En principio, estos autos no admiten recursos, salvo que generen un perjuicio irreparable o violen flagrantemente el derecho al debido proceso. Su importancia radica en que orientan el ritmo y la secuencia del proceso, pero no generan efectos jurídicos definitivos, sobre el objeto de la controversia. ")])])])],1)]),_vm._m(11),_vm._m(12),_vm._m(13),_vm._m(14),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Los recursos ordinarios contemplados en el Código General del Proceso, son la reposición, la apelación, la queja y la súplica, cada uno con características particulares, requisitos de procedencia y efectos procesales diferentes. ")]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("El manejo técnico y oportuno de estos recursos resulta importante para cualquier profesional del Derecho, pues de su adecuada interposición puede depender la suerte definitiva de los derechos e intereses en disputa.")]),_c('AcordionA',{attrs:{"tipo":"b","data-aos":"fade-left"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Recurso de reposición"}},[_c('p',[_vm._v("Se interpone ante el mismo funcionario judicial que profirió la providencia. Tiene como finalidad solicitarle que revoque, aclare o modifique su decisión, generalmente un auto, antes de que cause efectos definitivos. Procede contra autos de sustanciación e interlocutorios que no sean apelables, y su interposición debe hacerse dentro de los tres días siguientes a la notificación de la providencia o inmediatamente si la decisión se profiere en audiencia. Este recurso es útil para corregir errores formales o de interpretación, sin necesidad de escalar el proceso ante un superior. Por ejemplo, si un juez niega la práctica de una prueba por considerarla impertinente, y se estima que su admisión es clave para la defensa, puede solicitarse la reposición con argumentos precisos para que el juez reconsidere su decisión. Es importante resaltar que este recurso no tiene efecto suspensivo.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Recurso de apelación"}},[_c('p',[_vm._v("Es el recurso ordinario más relevante, puesto que permite que un juez superior jerárquico revise la decisión adoptada en primera instancia. Procede contra sentencias de primera instancia, así como contra autos interlocutorios expresamente señalados en el Artículo 321 del C.G.P. Su objetivo es obtener una revisión integral del caso en los aspectos que hayan sido objeto de inconformidad. La apelación puede ser concedida en efecto devolutivo es decir la decisión sigue ejecutándose, aunque se tramite la apelación o en efecto suspensivo la decisión no produce efectos hasta que se resuelva la apelación, dependiendo de la naturaleza del asunto. Por ejemplo, una sentencia que declara la nulidad de un contrato, puede ser apelada para que el Tribunal revise si hubo una interpretación adecuada de los hechos y del derecho aplicable. El recurso debe sustentarse, bien sea en audiencia o por escrito, y su omisión puede llevar a su inadmisión (Ortells Ramos & Bellido Penadés, 2016).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Recurso de queja"}},[_c('p',[_vm._v("Cuando el juez niega indebidamente el recurso de apelación, por ejemplo, al considerar erróneamente que no procede o que fue extemporáneo, la parte interesada puede acudir al recurso de queja. Este recurso se presenta directamente ante el juez superior, solicitando que se le ordene al inferior, conceder la apelación denegada. La queja cumple una función garantista del acceso a la segunda instancia, asegurando que no se cierre injustificadamente la puerta a una revisión superior. Por ejemplo, si un juez de primera instancia considera que una sentencia sobre responsabilidad extracontractual no admite apelación, pese a que sí cumple los requisitos, la parte afectada puede interponer la queja ante el tribunal, para que se reconozca su derecho.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Recurso de súplica"}},[_c('p',[_vm._v("Es una figura poco frecuente, pero de gran importancia dentro del procedimiento ante las altas cortes o tribunales. Procede contra ciertos autos dictados por un magistrado ponente, cuando este actúa sin intervención de la sala o sin haber agotado los trámites procesales correspondientes. El recurso busca que la sala o corporación, revise el auto en cuestión y, si es del caso, lo modifique o revoque. La súplica permite controlar decisiones unipersonales dentro de cuerpos colegiados, fortaleciendo la deliberación colectiva. Por ejemplo, si un magistrado ponente inadmite una demanda de revisión sin que se dé traslado a la sala, la parte puede interponer súplica para que se examine la legalidad del trámite. ")])])]),_vm._m(15),_vm._m(16),_vm._m(17),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-6 mb-3 mb-lg-0"},[_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("Para amplia la información sobre estos recursos, lo invitamos a escuchar el siguiente pódcast.")]),_c('TarjetaAudio',{staticClass:"color-acento-botones bg-color-white mb-3",attrs:{"texto":"Recursos extraordinarios","tiempo":"","audio":require('../../assets/curso/podcast/podcast1.mp3')}})],1),_vm._m(18)]),_c('div',{staticClass:"bg-full-width border-top actividad bg-color-actividad"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{attrs:{"id":"Actividad"}},[_c('Actividad',{attrs:{"cuestionario":_vm.cuestionario}})],1)])]),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("MATERIAL COMPLEMENTARIO")]),_c('div',{staticClass:"row material-complementario"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-7"},[_c('p',[_vm._v("Los invitamos a explorar el material complementario de este curso, en esta sección encontrará recursos que le permitirán profundizar y enriquecer su aprendizaje en los temas tratados en esta unidad.")]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/link.svg")}}),_c('a',{attrs:{"href":"http://www.secretariasenado.gov.co/senado/basedoc/ley_1564_2012.html","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Ley 1564 del 2012. Por medio de la cual se expide el Código General del Proceso y se dictan otras disposiciones. 12 de julio de 2012. D.O. 48.489. ")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticCla