 autos interlocutorios y autos de sustanciación. Cada una de estas clases cumple una función específica dentro del proceso, responde a un nivel distinto de intensidad jurídica, tiene implicaciones procesales propias y se encuentra sujeta a reglas particulares respecto de su motivación, notificación y control mediante recursos. ")])])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/63.png"),"data-aos":"zoom-in"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/64.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-color-3 mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("Cabe señalar que esta clasificación de las decisiones judiciales no solo tiene un valor académico o formal, sino que es indispensable para el ejercicio profesional del Derecho. Dependiendo del tipo de providencia, varía el término para interponer recursos, la posibilidad de impugnar la decisión, la obligatoriedad de su cumplimiento y su impacto dentro del proceso. Por ejemplo, confundir un auto interlocutorio con uno de sustanciación, puede llevar a que un recurso sea rechazado por improcedente o a que se pierda la oportunidad de corregir una decisión perjudicial para el cliente. ")])])]),_c('div',{staticClass:"col-lg-auto"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/65.png"),"alt":""}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-l