ios constituyen instrumentos jurídicos fundamentales para garantizar el principio de contradicción, el derecho a la defensa y la doble instancia, cuando esta resulta procedente. ")]),_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("Estos mecanismos permiten que una decisión judicial sea revisada por el mismo juez que la dictó o por un superior jerárquico, con el fin de corregir posibles errores, evitar decisiones arbitrarias y asegurar la correcta aplicación del derecho sustancial y procesal. Su existencia responde a la necesidad de legitimar el proceso y preservar la confianza de los ciudadanos, en la administración de justicia (Ortells Ramos & Bellido Penadés, 2016). ")])])]),_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/68.png")}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-6 p-4 mb-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"px-4"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/69.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("Todos estos recursos deben observar términos estrictos, usualmente de tres días, salvo disposiciones especiales, y su trámite está regido por los principios de celeridad, lealtad procesal y respeto a la legalidad. La interposición extemporánea, indebida o infundada, puede acarrear consecuencias adversas para la parte, incluyendo la imposibilidad de revisión posterior.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_3_3","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("3.3")]),_vm._v(" Recursos extraordinarios")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_